export default {
    namespaced: true,
    state: {
        id: "",
        name: "",
        email: "",
        avatar_url: ""
    },
    mutations: {
        setProfile(state, { id, name, email, avatar_url }) {
            state.id = id;
            state.name = name;
            state.email = email;
            state.avatar_url = avatar_url;
        }
    },
    actions: {
        getProfile({ commit, rootState }) {
            rootState.axios
                .get(
                    rootState.server +
                        "auth/github/user?access_token=" +
                        rootState.Vue.$cookies.get("access_token")
                )
                .then(res => {
                    const { id, name, email, avatar_url } = res.data;
                    commit("setProfile", { id, name, email, avatar_url });
                });
        }
    }
};
